<template>
  <div ref="content">
    <div v-for="img in images" :key="img.id" class="pdfcon">
      <img :src="img.imageUrl" class="imageforpdf" />
    </div>
    <v-card-actions>
      <v-btn class="mx-auto success" large tile @click="downloadPdf"
        >Download PDF</v-btn
      >
    </v-card-actions>
  </div>
</template>

<script>
import jsPDF from "jspdf";
export default {
  methods: {
    downloadPdf() {
  
      var doc = new jsPDF();
      this.images.forEach((img, i) => {
        var imgData = img.imageUrl;
        doc.addImage(imgData, "JPEG", 15, 40, 180, 200);
        if (i != this.images.length - 1) {
          doc.addPage();
        }
      });

      doc.save("pdfName" + ".pdf");
    
    },
  },
  computed: {
    images: {
      get() {
        return this.$store.state.images;
      },
      set(value) {
        this.$store.commit("updateList", value);
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.pdfcon {
  width: 100vw;
}
.imageforpdf {
  height: 100%;
  width: 100%;
}
</style>